@import url(https://fonts.googleapis.com/css2?family=Raleway|Ubuntu|Lato|Roboto|Oswald);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#loader {
  width: 100vw;
  height: 100vh;
  background-color: black;
  display: -webkit-flex;
  display: flex;

  -webkit-align-items: center;

          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
}
#box {
  display: -webkit-flex;
  display: flex;
}
#box p {
  text-align: center;
  font-size: 0.8rem;
  font-weight: bolder;
  line-height: 0.8rem;
  font-family: Roboto;
}
#box > div {
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  margin: 0px 2px;
}
#box > div > div {
  width: 20px;
  height: 20px;
}
#l {
  -webkit-animation: load 1s infinite alternate linear;
          animation: load 1s infinite alternate linear;
}
#o {
  -webkit-animation: load 1s 200ms infinite alternate linear;
          animation: load 1s 200ms infinite alternate linear;
}
#a {
  -webkit-animation: load 1s 400ms infinite alternate linear;
          animation: load 1s 400ms infinite alternate linear;
}
#d {
  -webkit-animation: load 1s 600ms infinite alternate linear;
          animation: load 1s 600ms infinite alternate linear;
}
#i {
  -webkit-animation: load 1s 800ms infinite alternate linear;
          animation: load 1s 800ms infinite alternate linear;
}
#n {
  -webkit-animation: load 1s 1s infinite alternate linear;
          animation: load 1s 1s infinite alternate linear;
}
#g {
  -webkit-animation: load 1s 1200ms infinite alternate linear;
          animation: load 1s 1200ms infinite alternate linear;
}
#l > div {
  background-color: rgb(184, 8, 8);
}
#o > div {
  background-color: rgb(20, 221, 70);
}
#a > div {
  background-color: rgb(218, 204, 20);
}
#d > div {
  background-color: rgb(43, 10, 228);
}
#i > div {
  background-color: rgb(9, 186, 240);
}
#n > div {
  background-color: rgb(134, 6, 240);
}
#g > div {
  background-color: rgb(228, 238, 229);
}
@-webkit-keyframes load {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }

  100% {
    -webkit-transform: scaleY(3.4);
            transform: scaleY(3.4);
  }
}
@keyframes load {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }

  100% {
    -webkit-transform: scaleY(3.4);
            transform: scaleY(3.4);
  }
}

* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}
body {
  background-color: rgba(155, 172, 148, 0.342);
}
form {
  max-width: 1000px;
  margin: auto;
  margin-top: 100px;
  padding: 30px;
  font-family: ubuntu, sans-serif;
}

form > p {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}
form > input {
  display: block;
  width: 100%;
  font-size: 1rem;
  height: 2rem;
  border: none;
  padding: 5px;
  outline: none;

  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.5);
}
h1 {
  font-size: 3rem;
  text-align: center;
  font-family: Roboto, sans-serif;
  padding: 20px;
  color: rgb(2, 148, 245);
  margin-bottom: 30px;
  font-weight: bolder;
}
#container {
  padding: 20px;
  max-width: 1000px;
  margin: auto;
  min-height: 100vh;
}
.dropdown {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  max-width: 1000px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  cursor: pointer;
  font-family: Raleway;
}
.dropdown > li,
select {
  position: relative;
  font-size: 1.3rem;
}
#results {
  text-align: right;
  padding: 10px;
  font-family: roboto;
  color: darkblue;
  font-size: 1.3rem;
  font-weight: bolder;
}
.tags {
  padding: 1px 4px;
  background-color: whitesmoke;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.521);
}
select {
  border: none;
  background-color: white;
  outline: none;
  padding: 3px;

  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.521);
}
select > option {
  font-size: 0.8em;
  height: 1em;
}

ul {
  list-style-type: none;
}
.dropdown > li > ul {
  display: none;
  font-size: 0.8em;
}
.dropdown > li:hover > ul {
  display: block;
}
.searchProblem {
  width: 60%;
}
#searchProblem {
  width: 100%;
  outline: none;
  height: 2rem;
  font-size: 1.4rem;
  border-radius: 1rem;
  padding: 10px;
}
#dropbox {
  position: absolute;
  z-index: 1;
  right: 0px;
  padding: 5px;
  border-radius: 10px;
  background-color: rgb(199, 211, 206);
}
#filterTags {
  height: 1.5rem;
  outline: none;
  padding: 5px;
  border-radius: 5px;
  font-size: 1rem;
}
.filterTags {
  height: 10em;
  overflow-y: scroll;
}
.filterTags > li {
  text-transform: capitalize;
}
.table-container {
  overflow-x: auto;
}

table {
  margin: auto;
  margin-top: 20px;
  border-collapse: collapse;
}
td,
th {
  white-space: nowrap;
  padding: 10px 10px;
  text-align: left;
  font-family: ubuntu;
  cursor: pointer;
}
th {
  font-size: 1.4rem;
  color: rgb(120, 5, 197);
  border-bottom: 1px solid rgba(77, 74, 74, 0.24);
}

tr:nth-child(even) {
  background-color: rgba(219, 219, 219, 0.685);
}

a {
  text-decoration: none;
  color: blue;
}
#number {
  font-weight: bolder;
  font-weight: bolder;
}
#status {
  color: green;
}
#rating {
  color: rgb(2, 104, 27);
}
#count {
  color: rgb(161, 91, 0);
}
td {
  font-size: 1.1rem;
}
#pages {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 30px;
}
#pages > p {
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  cursor: pointer;
}
#normal {
  background-color: green;
}
#highlight {
  background-color: greenyellow;
}
#diffrent {
  color: blue;
  margin: 10px 20px;
  cursor: pointer;
}
#notFound {
  font-size: 2rem;
  color: red;
  font-family: oswald, sans-serif;
  text-align: center;
}
.Showtags {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.Showtags > p {
  padding: 5px;
  border-radius: 3px;
  text-transform: capitalize;
  margin: 5px 10px;

  background-color: rgb(183, 183, 243);
}
.icon {
  color: red;

  cursor: pointer;
  margin-left: 3px;
  vertical-align: middle;
}
#end {
  text-align: center;
  color: rgb(9, 79, 184);
  margin-bottom: 20px;
}
@media (max-width: 700px) {
  html {
    font-size: 70%;
  }
  h1 {
    font-size: 2rem;
  }
  #container {
    padding: 10px;
  }
}

